* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 84px;
  font-weight: bold;
  line-height: 94px;
  height: 94px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 94px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
  color: white;
}

.status {
  margin-bottom: 10px;
  text-align: center;
  font-size: 44px;
}
.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
